import {Injectable, NgZone} from '@angular/core';
import {auth} from 'firebase/app';
import {AngularFireAuth} from '@angular/fire/auth';
import {AngularFirestore, AngularFirestoreDocument} from '@angular/fire/firestore';
import {Router} from '@angular/router';
import * as firebase from 'firebase';
import {AngularFireStorage} from 'angularfire2/storage';


@Injectable({
  providedIn: 'root'
})

export class AuthService {


  constructor(
    public afs: AngularFirestore,     // Inject Firestore service
    public afAuth: AngularFireAuth,   // Inject Firebase auth service
    public router: Router,
    public ngZone: NgZone,             // NgZone service to remove outside scope warning
    private storage: AngularFireStorage,
  ) {

    /* Saving user data in localstorage when
    logged in and setting up null when logged out */
    this.afAuth.authState.subscribe(user => {
      if (user) {
        this.userData = user;
        localStorage.setItem('user', JSON.stringify(this.userData));
        JSON.parse(localStorage.getItem('user'));
      } else {
        localStorage.setItem('user', null);
        JSON.parse(localStorage.getItem('user'));
      }
    });
    this.setMaxUser();
  }

  private userCount: number;
  public userData: any; // Save logged in user data
  public firstname;
  public lastname;
  public online;
  public job;
  public description;
  public profilePicture;
  private defaultPhotoURL;
  private providerId;

  // Returns true when user is looged in and email is verified
  get isLoggedIn(): boolean {
    const user = JSON.parse(localStorage.getItem('user'));
    return (user !== null && user.emailVerified !== false);
  }


  public getCurrentUser() {
    return this.afAuth.authState;
  }

// Sign in with email/password
  SignIn(email, password) {
    return this.afAuth.auth.signInWithEmailAndPassword(email, password)
      .then((result) => {
        this.ngZone.run(() => {
          this.router.navigate(['dashboard']);
        });
      }).catch((error) => {
        return (error.message);
      });
  }

  // Sign up with email/password
  SignUp(email, password) {
    return this.afAuth.auth.createUserWithEmailAndPassword(email, password).then((result) => {
      /* Call the SendVerificaitonMail() function when new user sign
      up and returns promise */
      this.SendVerificationMail().then(r => {});
      this.SetUserData(result.user);
    }).catch((error) => {
      window.alert(error.message);
    });
  }


  /* Setting up user data when sign in with username/password,
  sign up with username/password and sign in with social auth
  provider in Firestore database using AngularFirestore + AngularFirestoreDocument service */

  setFirstName(firstname) {
    this.firstname = firstname;
  }

  setLastName(lastname) {
    this.lastname = lastname;
  }

  setDescription(description) {
    this.description = description;
  }

  setJob(job) {
    this.job = job;
  }

  setProfilePicture(file) {
    this.profilePicture = file;
  }

  // Send email verfificaiton when new user sign up
  SendVerificationMail() {
    return this.afAuth.auth.currentUser.sendEmailVerification()
      .then(() => {
        this.router.navigate(['verify-email-address']);
      });
  }

  // Reset Forggot password
  ForgotPassword(passwordResetEmail) {
    return this.afAuth.auth.sendPasswordResetEmail(passwordResetEmail)
      .then(() => {
        window.alert('Password reset email sent, check your inbox.');
      }).catch((error) => {
        window.alert(error);
      });
  }


  GoogleAuth() {
    const google = new auth.GoogleAuthProvider();
    this.AuthLogin(google).then(() => {
    });
  }

  // Sign in with Facebooke
  FacebookAuth() {
    return this.AuthLogin(new auth.FacebookAuthProvider());
  }

  GithubAuth() {
    return this.AuthLogin(new auth.GithubAuthProvider());
  }

  AuthLogin(provider) {
    return this.afAuth.auth.signInWithPopup(provider)
      .then((result) => {
        this.providerId = provider.providerId;
        const usernew = result.additionalUserInfo.isNewUser;
        if (usernew === true) {
          this.splitName(result.user);
          this.SetUserData(result.user);
        }
        this.ngZone.run(() => {
          this.router.navigate(['dashboard']);
        });
      }).catch((error) => {
        console.log(error);
      });
  }

  /* Setting up user data when sign in with username/password,
  sign up with username/password and sign in with social auth
  provider in Firestore database using AngularFirestore + AngularFirestoreDocument service */


  SetUserData(user) {
    const countRef: AngularFirestoreDocument<any> = this.afs.doc(`users/userCount`);
    const userRef: AngularFirestoreDocument<any> = this.afs.doc(`users/${user.uid}`);

    const countData: any = {
      numberUsers: firebase.firestore.FieldValue.increment(1)
    };

    countRef.set(countData, {
      merge: true
    }).then(r => { });

    this.storage.ref('Users/Default_ProfilePicture/default_pic.png').getDownloadURL().toPromise().then(url => {
      this.defaultPhotoURL = url;
      const userData: any = {
        countId: ++this.userCount,
        uid: user.uid,
        email: user.email,
        description: this.description ? this.description : '',
        displayName: user.displayName ? user.displayName : this.firstname + ' ' + this.lastname,
        photoURL: this.providerId ? user.photoURL : this.defaultPhotoURL,
        emailVerified: user.emailVerified,
        job: this.job ? this.job : 'Project starter',
        firstname: this.firstname ? this.firstname : 'First Name',
        lastname: this.lastname ? this.lastname : 'Last Name'
      };

      if ( typeof this.profilePicture !== 'undefined') {
        const uploadTask = firebase.storage().ref(`Users/${user.uid}/profilePic/profilePic`).put(this.profilePicture);
        uploadTask.on('state_changed', (snapshot) => {
        }, (error) => {
          console.log(error);
        }, () => {
          uploadTask.snapshot.ref.getDownloadURL().then((url) => {
            firebase.auth().currentUser.updateProfile({
              photoURL: url
            }).then(() => {
              this.afs.collection('users').doc(user.uid).update({
                photoURL: url,
              });
            });
          });
        });
      }

      userRef.set(userData, {
        merge: true
      }).then(r => {console.log(r); });
    });
  }

  // Sign out
  SignOut() {
    return this.afAuth.auth.signOut().then(() => {
      localStorage.removeItem('user');
      this.router.navigate(['sign-in']);
    });
  }


  splitName(currentUser) {
    const splitName = currentUser.displayName.split(' ');
    this.firstname = splitName[0];
    this.lastname = splitName[1];
  }

  setMaxUser() {
    this.afs.collection('users/').doc('userCount').get().toPromise().then(
      doc => {
        if (doc.exists) {
          this.userCount = doc.data().numberUsers;
        }
      });
  }
}
